.gameImage {
  width: 100px;
  height: 100px;
}

.memoryMenu {
  margin: auto;
  width: 800px;
}

.gameTable {
  display: grid;
  margin: auto;
  width: 800px;
  height: 400px;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
}

.memoryButton {
  margin: 12px;
  margin-left: 0px;
}

span {
  margin-right: 12px;
}
