* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

h1,
p,
.link {
  margin-bottom: 12px;
}

h1 {
  font-size: 64px;
}

p {
  text-indent: 32px;
  font-size: 24px;
  word-spacing: 5px;
}

.container {
  margin: auto;
  width: 1200px;
  justify-content: center;
}

.link:link {
  padding-top: 6px;
  padding-bottom: 6px;
  text-align: center;
  text-transform: uppercase;
  display: inline-block;
  width: 20%;
  font-size: 20px;
  color: #000;
  text-decoration: none;
}

.link:hover {
  text-decoration: none;
  background-color: #000;
  color: #fff;
}

.link:visited {
  text-decoration: none;
}

.link-container {
  display: block;
  justify-content: center;
}
